<template>
  <div class="card my-5">
    <div class="card-body">
      <h5 class="text-center card-title">{{product.title}}</h5>
      <p class="text-center card-text text-muted display-4">${{Number(product.price).toFixed()}}</p>
      <button
        :disabled="isInCart"
        class="btn btn-info form-control"
        @click="$emit('addToCart', product)"
      >{{isInCart ? 'Added to cart': 'Add to cart'}}</button>
    </div>
  </div>
</template>

<script>
export default {
  props: ["product", "isInCart"],
  methods: {}
};
</script>

